<template>
   <div v-if="isLoading" class="flex flex-col gap-3 items-center justify-center w-full h-full">
      <LoaderIcon mode="standard" />
      <p>Please wait we are processing your payment</p>
   </div> 
   <div v-else-if="hasError" class="flex flex-col gap-3 items-center justify-center w-full h-full">
      <p>We encountered an error while processing your payment</p>
   </div> 
   <!--<div v-else class="flex flex-col py-24 px-11 lg:w-theme-dw md:mx-auto md:px-0">
      <h2 class="font-bold text-head-sm">Checkout API Response</h2>
      <div class="grid grid-cols-2 mt-12">
         <div class="col-span-1 min-h-96 border border-gray-300">
            <h3 class="text-center mt-2">Payload</h3>
            <div class="overflow-auto">{{ payload }}</div>
         </div>
         <div class="col-span-1 min-h-96 border-r border-b border-t border-gray-300">
            <h3 class="text-center mt-2">Response</h3>
            <div class="overflow-auto">{{ responsee }}</div>
         </div>
      </div>
   </div>-->
   <div v-else class="grid grid-cols-12 md:gap-10">
      <div v-if="feedType == 'success'" class="col-span-12 md:pb-12 flex flex-col items-center justify-center gap-6 mt-16">
         <div class="relative">
            <div class="flex items-center justify-center absolute -left-3 w-[100px] h-[100px] rounded-full border border-theme-gray-7">
               <font-awesome-icon size="2xl" icon="fa-solid fa-check" class="text-theme-active" />
            </div>
            <div class="bg-theme-gray-1 w-[100px] h-[100px] rounded-full"></div>
         </div>
         <h3 class="text-theme-gray-5 text-head-md-2 md:text-head-xl text-center flex flex-col gap-3">
            <div>Thank You</div>
         </h3>
         <div class="text-center flex flex-col gap-1">
            <div>A booking has been created and payment has been received</div>
         </div>
      </div>
      <div v-else-if="feedType == 'error'" class="col-span-12 md:pb-12 flex flex-col items-center justify-center gap-6 mt-16">
         <div class="relative">
            <div class="flex items-center justify-center absolute -left-3 w-[100px] h-[100px] rounded-full border border-theme-gray-7">
               <font-awesome-icon size="2xl" icon="fa-solid fa-check" class="text-theme-active" />
            </div>
            <div class="bg-theme-gray-1 w-[100px] h-[100px] rounded-full"></div>
         </div>
         <h3 class="text-theme-gray-5 text-head-md-2 md:text-head-xl text-center flex flex-col gap-3">
            <div>Thank You</div>
         </h3>
         <div class="text-center flex flex-col gap-1">
            <div>A booking has been created for you but we did not receive payment from your bank</div>
            <div>You will be contacted by our representative for further assistance</div>
         </div>
      </div>
   </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import LoaderIcon from '@/components/LoaderIcon.vue'
import { getBookingNumber, getPaymentSession, getPaymentTransaction, getSessionPrefix } from '@/utils/common'
import Payment from '@/composables/Payment'
import BookingService from '@/services/booking-service'
import { useRouter } from 'vue-router'

const props = defineProps(['currentStep'])
const isLoading = ref<boolean>(true)
const hasError = ref<boolean>(false)
const booking : string | null = getBookingNumber()
const transaction : string | null = getPaymentTransaction()
const session : string | null = getPaymentSession()
const { authorizePayment  } = Payment()
const payload = ref<any>(null)
const responsee = ref<any>(null)
const { finalizeeBooking  } = BookingService()
const feed = ref<any>()
const feedType = ref<'success' | 'error'>()
const router = useRouter()

onMounted(async () => {
   
   const bookingNumber = getBookingNumber() as string

   if (bookingNumber == null || bookingNumber == '') {
      router.push({name: 'Home'})
   } else {

      const response = await authorizePayment(session, booking, transaction)
      if (response.status == 200) {
   
         const responsee = response.data.response
   
         if (
            !responsee || 
            !responsee.response || 
            !responsee.response.gatewayCode || 
            responsee.response.gatewayCode != 'APPROVED' || 
            !responsee.result || 
            responsee.result != 'SUCCESS'
         ) {
            feed.value = responsee
            feedType.value = 'error'
         } else {
            feed.value = responsee.order
            feedType.value = 'success'
         }
   
         const finalResponse = await finalizeeBooking(bookingNumber, feedType.value, feed.value)
         isLoading.value = false
         if (finalResponse.success) {
   
            clearSessionData()
         } else {
            hasError.value = true
         }
      } else {
         console.log(response)
      }
   }

})

const clearSessionData = () => {
   localStorage.removeItem('vehitra_booking_transaction')
   localStorage.removeItem('vehitra_booking_session')
   localStorage.removeItem('vehitra_user_search')
   localStorage.removeItem('vehitra_booking_number')
   localStorage.removeItem('vehitra_selected_car')
}

</script>